/* ë°°ê²½ + ì¤‘ì•™ ì •ë ¬ */
.login-page {
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: flex-start; /* ìƒë‹¨ ì •ë ¬ */
  padding-top: 225px; /* ìƒë‹¨ ì—¬ë°± */
  background: linear-gradient(to right, #eef2f3, #8e9eab);
  font-family: "Apple SD Gothic Neo", "Noto Sans KR", sans-serif;
}

/* ì¹´ë“œí˜• ë¡œê·¸ì¸ ë°•ìŠ¤ */
.login-card {
  background-color: white;
  padding: 4rem 3rem;
  border-radius: 20px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
  width: 100%;
  max-width: 500px;
  display: flex;
  flex-direction: column;
  gap: 2rem;
  text-align: center;
}

/* íƒ€ì´í‹€ */
.login-title {
  font-size: 48px;
  font-weight: 800;
  color: #2c3e50;
}

/* ë¼ë²¨ */
.login-card label {
  font-size: 25px;
  font-weight: 600;
  color: #34495e;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

/* ì¸í’‹ */
.login-card input[type="tel"] {
  width: 100%;
  box-sizing: border-box; /* âœ… íŒ¨ë”© í¬í•¨í•œ ë„ˆë¹„ ê³„ì‚° */
  padding: 1.2rem;
  font-size: 20px;
  border-radius: 12px;
  border: 2px solid #bdc3c7;
  background-color: #f9f9f9;
}

.login-card input::placeholder {
  font-size: 18px;
  color: #aaa;
}

/* ë²„íŠ¼ */
.login-card button {
  padding: 1.2rem;
  font-size: 22px;
  font-weight: bold;
  color: white;
  background-color: #2980b9;
  border: none;
  border-radius: 15px;
  cursor: pointer;
  transition: background-color 0.3s, transform 0.2s;
}

.login-card button:hover {
  background-color: #3498db;
  transform: scale(1.05);
}

.login-card button:active {
  background-color: #2471a3;
  transform: scale(0.98);
}

/* ë°˜ì‘í˜• */
@media (max-width: 768px) {
  .login-title {
    font-size: 36px;
  }

  .login-card {
    padding: 2rem;
  }

  .login-card label {
    font-size: 18px;
  }

  .login-card input[type="tel"] {
    font-size: 18px;
  }

  .login-card button {
    font-size: 20px;
  }
}
